.submit {
  cursor: pointer;
  border: none;
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  color: #ffffff;
  background: #ff4d4d;
  border: 2px solid #ff4d4d;
  border-radius: 8px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 12px 24px;
  gap: 8px;
}

.submit:hover {
  background: #ffffff;
  color: #ff4d4d;
  border: 2px solid #ff4d4d;
}

.form > a {
  text-decoration: none;
}
.react-datepicker__input-container input {
  border: 1px solid rgb(203, 198, 198);
}
@media screen and (max-width: 992px) {
  .wrapper {
    margin: 10px auto;
    width: 89vw;
  }
}
@media screen and (max-width: 576px) {
  .wrapper {
    background-color: transparent;
    padding: 0;
  }
  .form,
  .form-2 {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
    gap: 1rem;
    padding: 20px 0;
    border-radius: 8px;
    background-color: rgba(255, 235, 235, 0.7);
  }
  .form div,
  .form-2 div {
    width: 90%;
  }
  .form div label,
  .form-2 div label {
    border: none;
  }
  .form a,
  .form-2 a {
    grid-column: span 4;
    width: 70vw;
    margin-left: -10vw;
  }
  .form-2 {
    width: 100%;
  }
  .submit {
    font-size: 14px;
    line-height: 20px;
    padding: 8px 20px;
    margin-left: 10px !important;
    width: fit-content !important;
  }
  .MuiButtonBase-root button {
    margin-left: 10px;
  }
  .booking-form-label {
    font-size: 14px;
  }
  .normal {
    font-size: 12px;
  }
  .css-o4b71y-MuiAccordionSummary-content {
    font-size: 16px;
  }
  .css-1uir9b3-MuiAccordionDetails-root {
    font-size: 13px !important;
  }
  .react-datepicker {
    display: none !important;
  }
  .react-datepicker__input-container input {
    min-width: 110%;
  }
}
