.myMessage {
  text-align: center;
  right: 40px;
  margin-top: 7.5%;
  color: white;
  font-weight: 800;
  font-size: 3rem;
}
.container {
  width: 82%;
  margin: auto;
  margin-top: 5%;
  box-sizing: border-box;
}
.Div1 {
  width: 49.5%;
  box-sizing: border-box;
  display: inline-block;
  /* border: 1px solid black; */
  padding: 3%;
}
.resoourceImg {
  height: 350px;
  width: 100%;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  object-fit: cover;
}
.resourceContent h3 {
  margin-top: 4%;
}
.resourceContent p {
  margin-top: 4%;
  line-height: 1.2;
}
.readmore {
  color: red;
}

@media screen and (max-width: 576px) {
  .myMessage {
    font-size: 30px;
    width: 80%;
    margin: auto;
  }
}

/* *************************individual Resource********************** */
.singleBlogContent {
  width: 80%;
  margin: auto;
  line-height: 24px;
  text-align: justify;
  margin-top: 2%;
  margin-bottom: 16%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.singleBlogContent h1 {
  font-weight: 600;
  font-size: 30px;
  margin: 10% 0 5% 0;
  padding: 0;
}

.singleBlogContent .img1{
  width: 80%;
}

.singleBlogContent .img-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}
.singleBlogContent .img-container > div {
  display: flex;
  gap: 1%;
  margin: 0 10%;
}
.singleBlogContent .img-container .img2 {
  width: 49.5%;
}
