.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
}

.nav-menu.active {
  width: 250px;
  height: 100vh;
  top: 65px;
  display: flex;
  justify-content: center;
  position: fixed;
  margin-top: 50px;
  left: 0;
  transition: 350ms;
}

.accounts hr {
  color: black;
  width: 100%;
  text-align: left;
}
.nav-text {
  /* text-decoration: none; */
  font-weight: 900;
  font-size: 20px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 40px;
  padding-bottom: 3%;
  border-radius: 4px;
  background-color: white;
  border: none;
  margin-left: 5px;
  /* border-color: white; */
}

.nav-text:hover {
  color: #ff6767;
}

.sel {
  color: #ff6767;
}

.nav-menu-items {
  top: 30px;
  width: 100%;
  padding: 0;
}

.accounts span {
  margin-left: 16px;
}

.profcomp {
  top: 60px;
  left: 250px;
  display: flex;
  position: absolute;
  flex-direction: column;
}

@media (max-width: 420px) {
  .profcomp {
    position: static;
    margin-bottom: 70px;
  }
  .nav-menu.active {
    position: static;
    overflow: hidden;
    height: 80px;
    width: 100%;
    margin-top: 60px;
  }
  .nav-menu-items {
    display: flex;
    padding: 20px;
  }
  .nav-text.sel,
  .nav-text {
    padding: 10px;
  }
}

.accimg {
  border-radius: 50%;
  margin-right: 50px;
}

.accbut {
  padding: 13px;
  border-radius: 5px;
  border: 1px solid #ea4235;
  font-size: 15px;
  background-color: #ea4235;
  color: white;
  cursor: pointer;
}

.accounts h2 {
