.content-wrapper {
  width: 98vw;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 400px;
  margin: 50px auto;
  background: #f5f5f5;
  backdrop-filter: blur(9.48954px);
}

.left {
  max-height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.right {
  max-height: 100%;
}

.content-container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin: 60px 0;
  gap: 30px;
}

.left-heading {
  width: 45%;
  font-family: "Rubik";
  font-style: normal;
  font-weight: 600;
  font-size: 30px;
  line-height: 66px;
  text-align: right;
}

.left-subsection-right {
  width: 45%;
}

.right-image {
  width: 100%;
  height: 100%;
}

.service-image-wrapper {
  margin-top: 70px;
  width: 100%;
  height: calc(100vh - 70px);
  overflow-y: hidden;
}

.service-image {
  width: 100%;
  height: 100%;
}

.text-on-image-container-2 {
  max-width: 100vw;
  position: relative;
}

@media screen and (max-width: 992px) {
  .content-wrapper {
    width: 100vw;
    height: 32vh;
    margin: 100px auto 120px;
  }
  .left {
    max-height: 82%;
  }
  .right {
    max-height: 82%;
  }
  .left-heading {
    width: 50%;
    font-size: 24px;
    line-height: 70px;
  }
  .content-container {
    margin: 40px 0;
  }
}

@media screen and (max-width: 768px) {
  .left-heading {
    width: 50%;
    font-size: 20px;
    line-height: 60px;
  }
  .left-subsection-right {
    font-size: 14px;
  }
}

@media screen and (max-width: 576px) {
  .content-wrapper {
    display: none;
  }
}
