#booking-contact-form {
  margin-top: 30px;
  max-width: 50vw;
  width: 50vw;
}

.name-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

.contact-form-input {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

#booking-contact-form label {
  display: block;
  font-family: "Rubik";
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  color: black;
}

#booking-contact-form input {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #374047;
}

#booking-contact-form > div {
  margin: 20px auto;
}

@media screen and (max-width: 576px) {
  #booking-contact-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
    padding: 20px 0;
    border-radius: 8px;
    background-color: rgba(255, 235, 235, 0.7);
    max-width: 80vw;
    width: 80vw;
    margin: auto;
    margin: 2rem 0;
  }
  .name-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .name-container input {
    width: 100%;
    font-size: 12px;
  }
  #booking-contact-form div {
    width: 73vw;
    margin: auto;
  }
  #booking-contact-form div label {
    border: none;
    font-size: 14px;
  }
  .css-7szws5-MuiButtonBase-root-MuiButton-root {
    width: 100% !important;
  }
  .contact-form-input {
    margin: 10px auto !important;
    gap: 20px;
  }
}
